.SectionsPage {
	position: relative;
	background-color: var(--main-bg);
	min-height: 100vh;
}
.SectionsPage__section-1 {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
	background: center / cover no-repeat url(../../images/Pages/Main_Image.webp);
	background-attachment: fixed;
}
.SectionsPage__title,
.SectionsPage__title2 {
	font-size: 120px;
	font-family: "Oswald", Arial, sans-serif;
	font-weight: 700;
	text-transform: uppercase;
	display: flex;
	color: var(--main-color);
	flex-direction: column;
	align-items: center;
	margin-bottom: 10px;
}
.SectionsPage__title2 {
	position: relative;
	align-items: flex-start;
	padding-top: 100px;
	font-size: 100px;
	z-index: 10;
}
.SectionsPage__title2 span {
	margin-bottom: 30px;
}

.SectionsPage__section-2 {
	position: relative;
	min-height: 100vh;
	width: 100%;
	background: 150px 0px no-repeat url(../../images/svg/pattern_2.svg),
		0px 200px no-repeat radial-gradient(ellipse at bottom, #e5d4de 0%, var(--main-bg) 70%);
}
.SectionsPage__content {
	position: absolute;
	top: 200px;
	right: -50px;
	width: 700px;
	min-height: 60vh;
	display: flex;
	z-index: 9;
}
.SectionsPage__box1 {
	align-self: flex-end;
	width: 200px;
	height: 380px;
	margin-right: 15px;
	z-index: 9;
}

.SectionsPage__img1 {
	width: 100%;
	margin-bottom: 10px;
}
.SectionsPage__box-text1 {
	width: 100%;
	font-family: "Raleway", Arial, sans-serif;
	margin-bottom: 10px;
}

.SectionsPage__box2 {
	width: 400px;
	height: 380px;
	z-index: 9;
}

.SectionsPage__box-text2 {
	width: 100%;
	font-family: "Raleway", Arial, sans-serif;
	margin-bottom: 10px;
}
.SectionsPage__img2 {
	width: 100%;
	margin-bottom: 10px;
}

.SectionsPage__box-text1 h4,
.SectionsPage__box-text2 h4 {
	font-size: 16px;
	font-weight: 700;
	margin-bottom: 10px;
}
.SectionsPage__box-text1 p,
.SectionsPage__box-text2 p {
	font-size: 14px;
	font-family: Arial, sans-serif;
	font-weight: 400;
}
